import React, { useState } from "react";

import { CopyToClipboard } from "react-copy-to-clipboard";

import url from "../../assets/img/pcks_england.png";

const Advertisement = () => {
  const [copied, setCopyState] = useState(false);

  console.log("Has it copied ... ", copied);
  const copyClass = copied ? "active" : "inactive";
  const copyText = copied ? "Copied 👍" : "Copy to clipboard";

  return (
    <div className="section-advert">
      <div className="token-title">
        <span className="available">Get @</span>
        <div className="icon">
          <img src={url} alt="advert" />
          <span>Pancake Swap</span>
        </div>
      </div>
      <div className="divider"></div>
      <div className="token-wrapper">
        <div className={`address ${copyClass}`}>
          0xCf8F5fCa88000D08b2Fbd1696c7D9b7AA6C173b6
        </div>
        <CopyToClipboard
          text="0xCf8F5fCa88000D08b2Fbd1696c7D9b7AA6C173b6"
          onCopy={() => setCopyState(true)}
        >
          <div className={`address-cpy ${copyClass}`}>
            <p>{copyText}</p>
          </div>
        </CopyToClipboard>
      </div>
    </div>
  );
};

export { Advertisement };
