import React, { useState, useRef, useEffect } from "react";
import Styled from "styled-components";

import { Link, Events } from "react-scroll";

import { Burger, Menu } from "../Mobile-Menu";
import { useOnClickOutside } from "../../hooks/useClickOutside";

import url from "../../assets/img/ichlrg.png";

const Wrapper = Styled.div`
    position: relative;
    height: 80px;
    z-index: 1000!important;
    background-color: transparent;
    background-color: hsla(0,0%,100%,.01);
    border-bottom: 1px solid hsl(230deg 7% 84% / 14%);
    text-align: center!important;
    top: 0;
`;

function Navigation() {
  const [open, setOpen] = useState(false);
  const node = useRef();
  const menuId = "main-menu";

  useOnClickOutside(node, () => setOpen(false));

  useEffect(() => {
    Events.scrollEvent.register("begin", function () {
      if (!open) {
        setOpen(false);
      }
    });
    Events.scrollEvent.register("end", function () {
      if (!open) {
        setOpen(false);
      }
    });
    return () => {
      Events.scrollEvent.remove("begin");
      Events.scrollEvent.remove("end");
    };
  }, []);

  useEffect(() => {
    if (open) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "unset";
    }
  }, [open]);

  return (
    <Wrapper>
      <div className="container">
        <div className="d-flex align-items-center desktop-navigation justify-content-lg-between flex-column flex-lg-row">
          <a href="/" className="navbrand d-none d-lg-inline-block">
            <img className="mr-3 nav-logo" src={url} alt="logo"></img>
          </a>
          <a href="/" className="navbrand d-lg-none text-white mb-5"></a>
          <div className="d-flex flex-column flex-lg-row text-center">
            <div className="flex-fill link">
              <Link
                activeClass="active"
                className="px-3"
                to="roadmap"
                spy={true}
                smooth={true}
                duration={500}
              >
                Roadmap
              </Link>
            </div>
            <div className="flex-fill link">
              <Link
                activeClass="active"
                className="px-3"
                to="tokenomics"
                spy={true}
                smooth={true}
                duration={500}
              >
                Tokenomics
              </Link>
            </div>
            <div className="flex-fill">
              <a
                href="https://exchange.pancakeswap.finance/#/swap?outputCurrency=0xCf8F5fCa88000D08b2Fbd1696c7D9b7AA6C173b6"
                className="px-3"
              >
                Buy
              </a>
            </div>
          </div>
        </div>
        <div className="d-flex d-lg-none justify-content-between align-items-center mobile-navigation">
          <img className="mr-3 nav-logo" src={url} alt="logo"></img>
          <div ref={node}>
            <Burger open={open} setOpen={setOpen} aria-controls={menuId} />
            <Menu open={open} setOpen={setOpen} id={menuId} />
          </div>
        </div>
      </div>
    </Wrapper>
  );
}

export { Navigation };
